    #resourceFilterSelect,
    #bannersFilterSelect,
    #newsFilterSelect,
    #tipsFilterSelect {
        width: 100%;
    }

    .bannersTableWrapper,
    .newsTableWrapper,
    .resourceCenterTableWrapper,
    .tipsTableWrapper {
        .editColumn {
            text-align: center;

            i {
                width: $fontSizeLarge;
                font-size: $fontSizeLarge;
                line-height: $fontSizeLarge;
                font-weight: $fontWeightBold;
            }
        }
    }

    .addNew {
        float: right;
        margin-top: 0;

        @media (max-width: $breakpoint_mobileMini_max) {
            float: none;
        }
    }

    // Audience Table
    #selectAudienceTable {
        tr {
            td{
                 position: relative;
            }

            .selectedColumn i {
                display: none;
                width: $fontSizeLarge;
                font-size: $fontSizeLarge;
                line-height: $fontSizeLarge;
                font-weight: $fontWeightBold;
                cursor: pointer;
            }

            .addToAudience {
                display: inline-block;
            }
        }
        .selectedColumn {
            text-align: center;
        }
        .selected {
            .addToAudience {
                display: none;
            }

            .selectedColumn i {
                display: inline-block;
            }
        }
        .audienceSelectCheckbox {
            position: absolute;
            top: 0;
            visibility: hidden;
        }
    }

    .audienceParticipants {
        width: 120px;

        ul {
            list-style: none;
            margin: 0 0 5px 5px;
            max-height: 200px;
            overflow-y: auto;

        }
    }
    .selectedAudienceTotal {
        margin-top: -1 * $baseLineHeight / 2;
        margin-bottom: $baseLineHeight;
    }


    //general page styles
    .spincover {
        position: absolute;
    }
    .bannerAddLink,
    .resourceCenterAddLink {
        clear: both;
    }
    .bannerAddLink .addDocBanner,
    .bannerAddLink .addUrlBanner,
    .resourceCenterAddLink .addDocResource,
    .resourceCenterAddLink .addUrlResource {
        clear: both;
        padding-right: 10px;

        i {
            padding-right: 7px;
        }
    }
    .resourceDisplayAttached,
    .bannerDisplayAttached {
        height: 20px;
        padding: 5px 0;
        position: relative;

        .spin {
            // display: block;
            height: $spinner_widthHeight / 2;
            left: 30px;
            margin: 0;
            position: absolute;
            width: $spinner_widthHeight / 2;
        }
    }
    .bannerDisplayLink,
    .resourceDisplayLink {
        /* NOTE: I (Joel) commented all this out because the styles were interfering with the (X) added as part of .removeLink
         * If these need to come back, I'm sorry. Some adjustment will need to be done to make the (X) display properly
         */
        // display: inline-block;
        // height: 20px;
        // overflow: hidden;
        // position: absolute;
        // text-overflow: ellipsis;
        // white-space: nowrap;
        // width: 100%;
        // z-index: 1;
    }
    .removeLink {
        cursor: pointer;
    }
    #resourceCenterLink,
    #bannerLink {
        background-color: transparent;
        border: none;
        box-shadow: none;
        height: 0;
        outline: none;
        padding: 0;
        position: absolute;
        top: -15px;
        width: 170px;
        z-index: 0;
    }
    .attachUrlResource,
    .attachUrlBanner {
        vertical-align: top;
    }
    .addDocBannerPopover,
    .addDocResourcePopover {
        .control-group {
            margin-bottom: 0;

            label {
                cursor: default;
            }
        }
        form {
            line-height: $baseLineHeight * 1.1;
            margin-top: 10px;

            input {
                height: 25px;
                line-height: $baseLineHeight * .85;
                outline: none;
            }
        }

    }
    .communicationsHelpText {
        color: #666666;
        font-size: $baseFontSize * .86;
    }
    #newsMessage {
        clear: both;
        margin-bottom: 15px;
    }
    .linkColumn,
    .storyColumn,
    .contentColumn {
        word-break: break-all;
        min-width: 80px;
    }
    .imageColumn {
        width: auto;
    }
    .bannerAddedContent,
    .newsAddedContent {
        .imageColumn img {
            height: 70px;
            width: auto;
            max-width: none;
        }
    }
    .resourceCenterAddedContent,
    .bannerAddedContent,
    .tipsAddedContent,
    .newsAddedContent {
        &.isEditing {
            @include opacity(0.5);
        }

        .table {
            margin-bottom: 0;

            i {
                width: $fontSizeLarge;
                font-size: $fontSizeLarge;
                line-height: $fontSizeLarge;
                font-weight: $fontWeightBold;
            }

            td {
                vertical-align: middle;
            }

            .editColumn,
            .remove {
                text-align: center;
            }

            .remove a[disabled]{
                cursor: not-allowed;
            }
        }

        .emptyMessage td {
            background-color: #cccccc;
        }
    }
    .addLanguageContent {
        margin: $baseLineHeight 0;

        a.disabled {
            cursor: not-allowed;
        }
    }

    //news and banner image select/upload
    .chooseImagesSection {
        float: left;
        margin-bottom: 15px;
        position: relative;

        .imageLargeWrapper {
            margin-bottom: $baseLineHeight;
            background-color: $brand_color_light;
            outline: 1px solid $brand_color_border;
            vertical-align: middle;
            text-align: center;

            img {
                display: block;
                width: 100%;
                height: 100%;
                vertical-align: top;
            }
        }
        .thumbnailWrapper {
            text-align: center;

            .selectedImageInput {
                position: absolute;
                right: 60px;
                top: 40px;
                visibility: hidden;
            }
        }
        #thumbnailContainer {
            margin-top: $baseLineHeight / 2;
            max-height: 3 * (1px + 100px + 1px + 8px); // magic calculation based on the thumbnail dimensions below
            overflow: hidden;
            position: relative;
        }
        #thumbnailPager {
            min-height: 36px; // height of a button

            .btn {
                margin-left: 0;

                & ~ .btn {
                    margin-right: 8px;
                }
            }
        }
        #thumbnailSelect {
            margin: 0 0 $baseLineHeight 0;
        }
        #thumbnailSelect li {
            display: inline-block;
            margin: 0 4px 8px;
            width: 235px; // magic number because we know the width of the images
            height: 100px; // magic number because we know the height of the images
            text-align: center;
            line-height: 100px; // magic number because we know the height of the images
            vertical-align: middle;
            list-style: none;
            cursor: pointer;
            border: 1px solid $brand_color_border;

            img {
                display: block;
                width: 100%;
                height: 100%;
            }
            .hide {
                display: none;
            }
            &.uploadContainer {
                background-color: $white;
                position: relative;

                a {
                    display: block;
                    height: 100%;
                    width: 100%;
                }
                img {
                    position: absolute;
                    left: 0;
                    top: 0;
                    @include opacity(0.25);
                }
                .icon-upload-1 {
                    font-size: $fontSizeLarge;
                }
                .upload-text {
                    display: inline-block;
                    line-height: normal;
                    vertical-align: middle;
                }
            }
            &.selected {
                border: 1px solid $brand_color_primary;
            }
        }
    }

    //richtext editor
    .contribCommentWrapper {
        @include clearfix();
        margin-top: $baseLineHeight;
        margin-right: -1 * ($baseLineHeight / 2);
        margin-left: -1 * ($baseLineHeight / 2);
        padding: $baseLineHeight / 2;
        position: relative;

        .mask {
            @include opacity(.5);
            background: black;
            bottom: 0;
            left: 0;
            right: 0;
            position: absolute;
            top: 0;
            z-index: 100;
        }
        .spin {
            left: 50%;
            margin: (-1 * $spinner_widthHeight / 2) 0 0 (-1 * $spinner_widthHeight / 2);
            position: absolute;
            top: 50%;
        }

        label {
            float: left;
        }

        .ToolBar {
            clear: left;
        }

        .commentTools {
            float: right;
            font-size: $fontSizeSmall;
            text-align: right;
            margin-bottom: $baseLineHeight / 4;

            .lt-ie8 & { // ie7
                width: 95%;
            }
            .remChars {
                font-weight: bold;
            }
        }
        .contribCommentInp {
            @include input-block-level();
            margin-bottom: 2px;

            .lt-ie8 & { // ie7
                width: 95%;
            }
        }
        @media (max-width: 480px) {

            .commentTools {
                clear: both;
                padding-top: 10px;
            }
        }
    }// .contribCommentWrapper

    .communicationsUploadImagePopover {
        z-index: 25000 !important;
    }

} /* /.page.communications */

.lt-ie9 {
    .bannerAddedContent,
    .newsAddedContent,
    .resourceCenterAddedContent,
    .tipsAddedContent {
        overflow: auto;
    }
}


#uploadImageModal {
    .imageUploadContainer {
        border-top: 1px solid $brand_color_border;
        padding: $baseLineHeight 0;
        overflow: auto;
    }

    .replaceImageTip {
        ul {
            margin-left: $baseLineHeight / 2;
        }
        input[type="file"] {
            height: $baseLineHeight;
            line-height: $baseLineHeight;
        }
    }

    .alert {
        margin-top: $baseLineHeight;

        p {
            margin: 0;
        }
    }

    .launchModule {
        margin: 0 0 $baseLineHeight 0;
        float: left;
        clear: left;
        @include clearfix();

        .item {
            position: relative;
            float: left;
        }

        // .wide-view overlays a fake news story title
        .wide-view {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            h3 {
                position: absolute;
                right: 0;
                bottom: 0;
                left: 0;
                margin: 0;
                padding: 2em 1em 1em 1em;
                color: $brand_color_white;
                font-weight: $fontWeightNormal;
                background: linear-gradient(to bottom, transparentize(black, 1) 0%, transparentize(black, 0.75) 100%); // used in _news.scss and _banners.scss and _xpromo.scss
            }
        }

        // .safe-area shows where it's OK to have banner artwork
        .safe-area {
            position: absolute;
            top: 25%;
            right: 0;
            bottom: 25%;
            left: 0;
            color: $brand_color_success;
            border: 2px dashed $brand_color_success;

            &::before,
            &::after {
                position: absolute;
                top: -50%;
                right: -2px;
                bottom: 100%;
                left: -2px;
                margin-top: -4px;
                margin-bottom: 2px;
                content: '';
                background: black;
                opacity: 0.5;
            }
            &::after {
                top: 100%;
                bottom: -50%;
                margin-top: 2px;
                margin-bottom: -4px;
            }

            h4 {
                position: absolute;
                bottom: 100%;
                left: 0;
                margin: 0.125em 0.5em;
            }
        }

        // these max-heights are all magical and based on the known pixel dimensions of each image
        &.size-max {
            img {
                max-height: 600px;
            }
        }
        &.size-default {
            img {
                max-height: 300px;
            }
        }
        &.size-mobile {
            img {
                max-height: 150px;
            }
            h3 {
                padding: 1em 0.5em 0.5em 0.5em;
                font-size: $headline3fontSize_mobile;
            }
            h4 {
                font-size: $headline4fontSize_mobile;
            }
        }

        .replaceImage {
            position: relative;
            float: left;
            clear: both;
            padding: $g5ModulePadding / 2 $g5ModulePadding;
            background: $communications_uploadPreviewReplaceBgColor;
            cursor: pointer;
            @if $communications_uploadPreviewReplaceBorderColor {
                border: 1px solid $communications_uploadPreviewReplaceBorderColor;
                border-top: 0;
            }
        }

        .spincover {
            position: absolute;
            z-index: 10;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            @include safeBkgs(rgba(255,255,255,0.5));

            .spin {
                position: absolute;
                top: 50%;
                left: 50%;
                margin: (-1 * $spinner_widthHeight / 2) 0 0 (-1 * $spinner_widthHeight / 2);
                padding: 0;
                background: none;
                filter: none;
            }
        }
    }
    .pageUploadPreview .launchModule {
        margin: 0;

        img {
            position: relative;
        }
    }
    @media (max-width: 767px) {
        .pageUploadPreview {
            .span2 {
                @include clearfix();
            }
            .grid-dimension-page {
                float: left;

                img {
                    float: left;
                }
            }
        }
    }

    .spin {
        display: block;
        height: $spinner_widthHeight;
        margin: 0 auto;
        position: relative;
        top: 30%;
        width: $spinner_widthHeight;
    }
    .newsUploadImage,
    .bannerUploadImage {
        overflow: hidden;

        label {
            display: inline;
        }

        input[type="file"] {
            line-height: $baseLineHeight;
        }
    }

    .uploadRequirements {
        color: #999;
        display: block;
        font-size: $baseFontSize * .86;
        line-height: $baseLineHeight * .7;
        margin-top: 10px;
    }
}
// /#uploadImageModal