// App-specific variables
// =========================
$hero_bgColor: $brand_color_primary !default;
$hero_bgImg: false !default;
$hero_bgImgPosition: 50% 50% !default;
$hero_bgImgRepeat: no-repeat !default;
$hero_bgImgSize: cover !default;
$hero_bgImgAttachment: scroll !default;
$hero_bgImgAllowedNotHome: false !default;

// App-specific code
// ==================================================


/* ----- GLOBAL APP STYLES ----- */
// Note that every module and page and related instance of this app should be namespaced with the appName
.hero {

} /* /.hero */

/* ----- MODULE STYLES ----- */
.launchModule.hero {

    >.module-liner {
        position: relative;
        padding: 0;
        height: 144px;
        max-height: 144px;
        background: $hero_bgColor;
        transition-property: height, width;
        transition-duration: 100ms;

        @if $hero_bgImg and $hero_bgImgAllowedNotHome {
            @include safeImgUrls($hero_bgImg, '../img/', $property: 'background-image');
            background-position: $hero_bgImgPosition;
            background-repeat: $hero_bgImgRepeat;
            background-size: $hero_bgImgSize;
            background-attachment: $hero_bgImgAttachment;
        }

        .is-home & {
            height: auto;
            min-height: 144px;
            max-height: 600px;

            @if $hero_bgImg and $hero_bgImgAllowedNotHome == false {
                @include safeImgUrls($hero_bgImg, '../img/', $property: 'background-image');
                background-position: $hero_bgImgPosition;
                background-repeat: $hero_bgImgRepeat;
                background-size: $hero_bgImgSize;
                background-attachment: $hero_bgImgAttachment;
            }
        }
    }

    // overwrite min-height when an inner module is loaded inside
    &.inner-module-loaded{
        >.module-liner {
                height: auto;
                min-height: 144px;
                max-height: 100%;
        }
    }


    .paxSearchStartView {
        // transition-property: display;
        // transition-duration: $global_sidebar_transition_speed;
        // background: green;
        height: 100%;
        display: none;
    }
    .paxSearchOuterWrap {
        // background: orange;
        position: absolute;
        top: 50%;
        right: 30px;
        left: 30px;
        height: 40px;
        margin: -20px 0 0 0;
        line-height: 40px;
        text-align: center;
        vertical-align: middle;
    }
    .paxSearchWrap {
        display: inline-block;
        padding: 0;
        margin: 0;
        width: 100%;
        max-width: 468px;
        position: relative;
    }
    .paxSearchInput::-ms-clear {
      width : 0;
      height: 0;
    }
    .paxSearchInput {
        margin: 0 -8px;
        width: 100%;
    }
    .icon-magnifier-1 {
        position: absolute;
        // top: 10; leftover??
        top: 15px;
        margin-left: -15px;
        font-weight: bold;
        right:0;
    }
    .ezRecView {
        height: 100%;
        position:initial;
        display: block;
        min-height: 144px;
    }
} /* /.launchModule.hero */

/* ----- PAGE STYLES ----- */
.page.hero {

} /* /.page.hero */
